"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { createClient } from "@/lib/supabase/client"
import { ArrowLeft, TrendingUp, DollarSign, ShoppingCart, Clock, UtensilsCrossed, CreditCard, Building2, Users } from "lucide-react"

type PeriodType = 'daily' | 'weekly' | 'monthly' | 'yearly'

interface DateRange {
  start: Date
  end: Date
  day: string
  month: string
  fullLabel: string
}

interface TableAnalytics {
  tableNumber: number
  orders: number
  revenue: number
  avgOrder: number
  customers: number
}

interface TopItem {
  name: string
  revenue: number
  count: number
}

export default function AnalyticsPage() {
  const router = useRouter()
  const supabase = createClient()
  
  const [loading, setLoading] = useState(true)
  const [restaurant, setRestaurant] = useState<any>(null)
  const [allRestaurants, setAllRestaurants] = useState<any[]>([])
  const [period, setPeriod] = useState<PeriodType>('daily')
  const [dates, setDates] = useState<DateRange[]>([])
  const [selected, setSelected] = useState(0)
  
  const [stats, setStats] = useState({
    revenue: 0,
    orders: 0,
    avgOrder: 0,
    cashRevenue: 0,
    qrisRevenue: 0,
    transferRevenue: 0,
    peakHour: '-',
    topTable: 0
  })
  const [topItems, setTopItems] = useState<TopItem[]>([])
  const [tableAnalytics, setTableAnalytics] = useState<TableAnalytics[]>([])

  // Initialize: Load user and restaurants
  useEffect(() => {
    async function init() {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push('/login')
        return
      }

      // Load all restaurants
      const { data: restaurants } = await supabase
        .from('restaurants')
        .select('*')
        .eq('owner_id', user.id)

      if (!restaurants || restaurants.length === 0) {
        router.push('/dashboard')
        return
      }

      setAllRestaurants(restaurants)
      
      // Check localStorage for selected restaurant
      const savedRestaurantId = localStorage.getItem('selected_restaurant_id')
      if (savedRestaurantId) {
        const saved = restaurants.find(r => r.id === savedRestaurantId)
        if (saved) {
          setRestaurant(saved)
        } else {
          setRestaurant(restaurants[0])
        }
      } else {
        setRestaurant(restaurants[0])
      }
    }

    init()
  }, [])

  // Generate date ranges when period changes
  useEffect(() => {
    const now = new Date()
    const ranges: DateRange[] = []
    const months = ['Jan', 'Feb', 'Mar', 'Apr', 'Mei', 'Jun', 'Jul', 'Agu', 'Sep', 'Okt', 'Nov', 'Des']

    if (period === 'daily') {
      for (let i = 29; i >= 0; i--) {
        const d = new Date(now)
        d.setDate(now.getDate() - i)
        ranges.push({
          start: new Date(d.setHours(0, 0, 0, 0)),
          end: new Date(d.setHours(23, 59, 59, 999)),
          day: String(d.getDate()),
          month: months[d.getMonth()],
          fullLabel: `${d.getDate()} ${months[d.getMonth()]}`
        })
      }
    } else if (period === 'weekly') {
      for (let i = 11; i >= 0; i--) {
        const end = new Date(now)
        end.setDate(now.getDate() - (i * 7))
        const start = new Date(end)
        start.setDate(end.getDate() - 6)
        ranges.push({
          start: new Date(start.setHours(0, 0, 0, 0)),
          end: new Date(end.setHours(23, 59, 59, 999)),
          day: `${start.getDate()}-${end.getDate()}`,
          month: months[end.getMonth()],
          fullLabel: `${start.getDate()}-${end.getDate()} ${months[end.getMonth()]}`
        })
      }
    } else if (period === 'monthly') {
      for (let i = 11; i >= 0; i--) {
        const d = new Date(now.getFullYear(), now.getMonth() - i, 1)
        ranges.push({
          start: new Date(d.getFullYear(), d.getMonth(), 1, 0, 0, 0, 0),
          end: new Date(d.getFullYear(), d.getMonth() + 1, 0, 23, 59, 59, 999),
          day: months[d.getMonth()],
          month: String(d.getFullYear()),
          fullLabel: `${months[d.getMonth()]} ${d.getFullYear()}`
        })
      }
    } else {
      for (let i = 4; i >= 0; i--) {
        const year = now.getFullYear() - i
        ranges.push({
          start: new Date(year, 0, 1, 0, 0, 0, 0),
          end: new Date(year, 11, 31, 23, 59, 59, 999),
          day: String(year),
          month: '',
          fullLabel: String(year)
        })
      }
    }

    setDates(ranges)
    setSelected(ranges.length - 1) // Select most recent
  }, [period])

  // Load analytics data when restaurant or date range changes
  useEffect(() => {
    if (restaurant && dates.length > 0) {
      loadAnalytics()
    }
  }, [restaurant, dates, selected])

  async function loadAnalytics() {
    if (!restaurant || dates.length === 0) return
    
    setLoading(true)
    try {
      const range = dates[selected]
      
      console.log('üìÖ Fetching analytics:', {
        restaurant: restaurant.name,
        period: range.fullLabel,
        start: range.start.toISOString(),
        end: range.end.toISOString()
      })

      // Fetch paid orders within date range
      const { data: orders } = await supabase
        .from('orders')
        .select('*, order_items(*)')
        .eq('restaurant_id', restaurant.id)
        .eq('payment_status', 'paid')
        .gte('paid_at', range.start.toISOString())
        .lte('paid_at', range.end.toISOString())

      if (!orders) {
        console.log('‚ùå No orders found')
        setLoading(false)
        return
      }

      console.log(`‚úÖ Found ${orders.length} orders`)

      // Calculate revenue and orders
      const revenue = orders.reduce((sum, o) => {
        const amount = o.total || 0
        return o.status === 'cancelled' ? sum - amount : sum + amount
      }, 0)
      const count = orders.filter(o => o.status !== 'cancelled').length

      // Payment methods breakdown
      const cashOrders = orders.filter(o => ['cash', 'CASH'].includes(o.payment_method))
      const qrisOrders = orders.filter(o => ['qris', 'QRIS'].includes(o.payment_method))
      const transferOrders = orders.filter(o => ['transfer', 'TRANSFER', 'bank_transfer', 'BANK_TRANSFER'].includes(o.payment_method))
      
      const cash = cashOrders.reduce((s, o) => o.status === 'cancelled' ? s - (o.total || 0) : s + (o.total || 0), 0)
      const qris = qrisOrders.reduce((s, o) => o.status === 'cancelled' ? s - (o.total || 0) : s + (o.total || 0), 0)
      const transfer = transferOrders.reduce((s, o) => o.status === 'cancelled' ? s - (o.total || 0) : s + (o.total || 0), 0)

      // Top selling items
      const itemMap = new Map<string, { revenue: number; count: number }>()
      orders.filter(order => order.status !== 'cancelled').forEach((order: any) => {
        order.order_items?.forEach((item: any) => {
          const key = item.name || 'Item'
          const existing = itemMap.get(key) || { revenue: 0, count: 0 }
          itemMap.set(key, {
            revenue: existing.revenue + (item.price * item.quantity),
            count: existing.count + item.quantity
          })
        })
      })
      const items = Array.from(itemMap.entries())
        .map(([name, data]) => ({ name, ...data }))
        .sort((a, b) => b.revenue - a.revenue)
        .slice(0, 10)

      // Table analytics
      const tableMap = new Map<number, { orders: number; revenue: number; customers: number }>()
      orders.filter(o => o.status !== 'cancelled').forEach((order: any) => {
        if (order.table_number) {
          const existing = tableMap.get(order.table_number) || { orders: 0, revenue: 0, customers: 0 }
          tableMap.set(order.table_number, {
            orders: existing.orders + 1,
            revenue: existing.revenue + (order.total || 0),
            customers: existing.customers + 1
          })
        }
      })
      const tables = Array.from(tableMap.entries())
        .map(([tableNumber, data]) => ({
          tableNumber,
          orders: data.orders,
          revenue: data.revenue,
          avgOrder: data.orders > 0 ? data.revenue / data.orders : 0,
          customers: data.customers
        }))
        .sort((a, b) => b.revenue - a.revenue)

      // Peak hour
      const hourMap = new Map<number, number>()
      orders.forEach((order: any) => {
        if (order.paid_at) {
          const hour = new Date(order.paid_at).getHours()
          hourMap.set(hour, (hourMap.get(hour) || 0) + 1)
        }
      })
      const peakHourData = Array.from(hourMap.entries()).sort((a, b) => b[1] - a[1])[0]
      const peakHour = peakHourData ? `${peakHourData[0].toString().padStart(2, '0')}:00` : '-'

      // Top table by revenue
      const topTable = tables[0]?.tableNumber || 0

      setStats({
        revenue,
        orders: count,
        avgOrder: count > 0 ? revenue / count : 0,
        cashRevenue: cash,
        qrisRevenue: qris,
        transferRevenue: transfer,
        peakHour,
        topTable
      })
      setTopItems(items)
      setTableAnalytics(tables)

    } catch (error) {
      console.error('‚ùå Error loading analytics:', error)
    } finally {
      setLoading(false)
    }
  }

  function handleRestaurantChange(restaurantId: string) {
    const selected = allRestaurants.find(r => r.id === restaurantId)
    if (selected) {
      setRestaurant(selected)
      localStorage.setItem('selected_restaurant_id', restaurantId)
      // Dispatch custom event to notify theme provider
      window.dispatchEvent(new Event('restaurantChanged'))
    }
  }

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount)
  }

  if (!restaurant || dates.length === 0) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[var(--color-accent)]"></div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-50 shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <button
                onClick={() => router.back()}
                className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
              >
                <ArrowLeft className="w-5 h-5" />
              </button>
              <h1 className="text-2xl font-bold text-gray-900">Analytics</h1>
            </div>
            {allRestaurants.length > 1 && (
              <select 
                value={restaurant.id}
                onChange={(e) => handleRestaurantChange(e.target.value)}
                className="px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium focus:outline-none focus:ring-2 focus:ring-[var(--color-accent)]"
              >
                {allRestaurants.map(r => (
                  <option key={r.id} value={r.id}>{r.name}</option>
                ))}
              </select>
            )}
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Period Selector */}
        <div className="mb-6">
          <p className="text-sm font-semibold text-gray-500 mb-3">PERIODE</p>
          <div className="flex gap-2 overflow-x-auto pb-2">
            {(['daily', 'weekly', 'monthly', 'yearly'] as PeriodType[]).map((p) => (
              <button
                key={p}
                onClick={() => setPeriod(p)}
                className={`px-4 py-2 rounded-lg font-medium whitespace-nowrap transition-all ${
                  period === p
                    ? 'bg-[var(--color-accent)] text-white shadow-md'
                    : 'bg-white text-gray-700 hover:bg-gray-50 border border-gray-200'
                }`}
              >
                {p === 'daily' && 'Harian'}
                {p === 'weekly' && 'Mingguan'}
                {p === 'monthly' && 'Bulanan'}
                {p === 'yearly' && 'Tahunan'}
              </button>
            ))}
          </div>
        </div>

        {/* Date Selector */}
        <div className="mb-8">
          <p className="text-sm font-semibold text-gray-500 mb-3">
            {period === 'daily' ? 'TANGGAL' : period === 'weekly' ? 'MINGGU' : period === 'monthly' ? 'BULAN' : 'TAHUN'}
          </p>
          <div className="flex gap-3 overflow-x-auto pb-2">
            {dates.map((date, idx) => (
              <button
                key={idx}
                onClick={() => setSelected(idx)}
                className={`px-4 py-3 rounded-lg transition-all whitespace-nowrap ${
                  selected === idx
                    ? 'bg-[var(--color-accent)] text-white shadow-md'
                    : 'bg-white text-gray-700 hover:bg-gray-50 border border-gray-200'
                }`}
              >
                <div className="text-center">
                  <div className={`text-lg font-bold ${selected === idx ? 'text-white' : 'text-gray-900'}`}>
                    {date.day}
                  </div>
                  {date.month && (
                    <div className={`text-xs ${selected === idx ? 'text-white' : 'text-gray-500'}`}>
                      {date.month}
                    </div>
                  )}
                </div>
              </button>
            ))}
          </div>
        </div>

        {loading ? (
          <div className="flex items-center justify-center py-20">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-[var(--color-accent)]"></div>
          </div>
        ) : (
          <>
            {/* Main Revenue Card */}
            <div className="bg-gradient-to-br from-[var(--color-accent)] to-[var(--color-accent-hover)] rounded-2xl p-8 mb-8 text-white shadow-lg">
              <p className="text-sm opacity-90 mb-2">{dates[selected]?.fullLabel}</p>
              <h2 className="text-4xl font-bold mb-4">
                {formatCurrency(stats.revenue)}
              </h2>
              <div className="flex items-center gap-8">
                <div>
                  <p className="text-2xl font-bold">{stats.orders}</p>
                  <p className="text-sm opacity-90">Pesanan</p>
                </div>
                <div className="w-px h-12 bg-white/20"></div>
                <div>
                  <p className="text-2xl font-bold">{formatCurrency(stats.avgOrder)}</p>
                  <p className="text-sm opacity-90">Rata-rata</p>
                </div>
              </div>
            </div>

            {/* Payment Methods */}
            <div className="bg-white rounded-2xl p-6 mb-8 shadow-sm border border-gray-100">
              <h3 className="text-sm font-semibold text-gray-500 mb-4">METODE PEMBAYARAN</h3>
              <div className="space-y-3">
                <div className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                  <div className="flex items-center gap-3">
                    <CreditCard className="w-5 h-5 text-gray-600" />
                    <div>
                      <p className="font-semibold text-gray-900">üíµ Tunai</p>
                      <p className="text-xs text-gray-600">
                        {stats.revenue > 0 ? `${((stats.cashRevenue / stats.revenue) * 100).toFixed(0)}%` : '0%'}
                      </p>
                    </div>
                  </div>
                  <p className="text-lg font-bold text-gray-900">
                    {formatCurrency(stats.cashRevenue)}
                  </p>
                </div>

                <div className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                  <div className="flex items-center gap-3">
                    <CreditCard className="w-5 h-5 text-gray-600" />
                    <div>
                      <p className="font-semibold text-gray-900">üì± QRIS</p>
                      <p className="text-xs text-gray-600">
                        {stats.revenue > 0 ? `${((stats.qrisRevenue / stats.revenue) * 100).toFixed(0)}%` : '0%'}
                      </p>
                    </div>
                  </div>
                  <p className="text-lg font-bold text-gray-900">
                    {formatCurrency(stats.qrisRevenue)}
                  </p>
                </div>

                <div className="flex items-center justify-between p-4 bg-gray-50 rounded-xl">
                  <div className="flex items-center gap-3">
                    <CreditCard className="w-5 h-5 text-gray-600" />
                    <div>
                      <p className="font-semibold text-gray-900">üè¶ Transfer</p>
                      <p className="text-xs text-gray-600">
                        {stats.revenue > 0 ? `${((stats.transferRevenue / stats.revenue) * 100).toFixed(0)}%` : '0%'}
                      </p>
                    </div>
                  </div>
                  <p className="text-lg font-bold text-gray-900">
                    {formatCurrency(stats.transferRevenue)}
                  </p>
                </div>
              </div>
            </div>

            {/* Top Items */}
            {topItems.length > 0 && (
              <div className="bg-white rounded-2xl p-6 mb-8 shadow-sm border border-gray-100">
                <h3 className="text-sm font-semibold text-gray-500 mb-4">ITEM TERLARIS</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {topItems.slice(0, 4).map((item, idx) => (
                    <div key={idx} className="p-4 bg-gray-50 rounded-xl">
                      <div className="flex items-start justify-between mb-2">
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-1">
                            <span className="w-6 h-6 bg-[var(--color-accent)] text-white rounded-full flex items-center justify-center text-xs font-bold">
                              {idx + 1}
                            </span>
                            <p className="font-semibold text-gray-900 line-clamp-1">{item.name}</p>
                          </div>
                          <p className="text-sm text-gray-600">{item.count} terjual</p>
                        </div>
                        <p className="text-lg font-bold text-[var(--color-accent)] ml-4">
                          {formatCurrency(item.revenue)}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Table Analytics */}
            {tableAnalytics.length > 0 && (
              <div className="bg-white rounded-2xl p-6 mb-8 shadow-sm border border-gray-100">
                <h3 className="text-sm font-semibold text-gray-500 mb-4">PERFORMA MEJA</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {tableAnalytics.slice(0, 4).map((table, idx) => (
                    <div key={table.tableNumber} className="p-4 bg-gray-50 rounded-xl">
                      <div className="flex items-start justify-between mb-2">
                        <div className="flex-1">
                          <div className="flex items-center gap-2 mb-1">
                            <span className="w-6 h-6 bg-[var(--color-accent)] text-white rounded-full flex items-center justify-center text-xs font-bold">
                              {idx + 1}
                            </span>
                            <p className="font-semibold text-gray-900">Meja {table.tableNumber}</p>
                          </div>
                          <div className="flex items-center gap-4 text-sm text-gray-600">
                            <span>{table.customers} pelanggan</span>
                            <span>‚Ä¢</span>
                            <span>{table.orders} pesanan</span>
                          </div>
                        </div>
                        <p className="text-lg font-bold text-[var(--color-accent)] ml-4">
                          {formatCurrency(table.revenue)}
                        </p>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Insights */}
            <div className="bg-white rounded-2xl p-6 shadow-sm border border-gray-100">
              <h3 className="text-sm font-semibold text-gray-500 mb-4">INSIGHTS</h3>
              <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                <div className="p-4 bg-yellow-50 rounded-xl text-center">
                  <Clock className="w-8 h-8 text-yellow-600 mx-auto mb-2" />
                  <p className="text-2xl font-bold text-gray-900">{stats.peakHour}</p>
                  <p className="text-sm text-gray-600">Waktu Tersibuk</p>
                </div>

                <div className="p-4 bg-gray-50 rounded-xl text-center">
                  <Building2 className="w-8 h-8 text-gray-600 mx-auto mb-2" />
                  <p className="text-2xl font-bold text-gray-900">
                    {stats.topTable > 0 ? `Meja ${stats.topTable}` : '-'}
                  </p>
                  <p className="text-sm text-gray-600">Meja Terlaris</p>
                </div>

                <div className="p-4 bg-green-50 rounded-xl text-center">
                  <DollarSign className="w-8 h-8 text-green-600 mx-auto mb-2" />
                  <p className="text-2xl font-bold text-gray-900">{formatCurrency(stats.avgOrder)}</p>
                  <p className="text-sm text-gray-600">Avg Order Value</p>
                </div>
              </div>
            </div>
          </>
        )}
      </main>
    </div>
  )
}
